.toolbar {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background: #000000;
    height: 83px;
    z-index: 1030;
    padding: 0 20px;
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar__logo {
    margin-left: 1rem;
}

.toolbar__logo a {
    color: #ffffff;
    text-decoration: none;
}

.spacer {
    flex: 1;
}

.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-items li {
    padding: 0 0.5rem;
}

.toolbar_navigation-items a {
    color: #ffffff;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
    color: #ffd715;
}

@media screen and (max-width: 600px) {
    .toolbar_navigation-items {
        display: none;
    }
}

@media screen and (min-width: 600px) {
    .toolbar_toggle-button {
        display: none;
    }

    .toolbar__logo {
        margin-left: 0;
    }
}