$darkColor1: #121212;
$lightColor1: #ffd715;
$lightColor2: rgb(5, 159, 185);
$lightColor3: #674235;
$white: #ffffff;
$black: #000000;

@font-face {
  src: url('/assets/fonts/coolvetica.ttf');
  font-family: coolvetica;
} 

@font-face {
  src: url('/assets/fonts/helvetica.ttf');
  font-family: helvetica;
}

@font-face {
  src: url('/assets/fonts/Lato-Regular.ttf');
  font-family: lato;
} 

@mixin translateY($e) {
  -webkit-transform: translateY($e);
    -moz-transform: translateY($e);
          transform: translateY($e);
}

@mixin transition($e) {
  -webkit-transition:$e;
    -moz-transition:$e;
          transition:$e;
}

*, *::before, *::after {
  box-sizing: border-box;
}

/*Wb-styling-start*/

.wb-font-custom {
  color: $lightColor1;
}

.wb-custom {
  background: $lightColor2;
}

small {
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 4px;
}
  
.wb-item{
  margin:1rem;
  box-shadow: 0.3rem 0.4rem 0.4rem rgba(0, 0, 0, 0.4);
  overflow: hidden;

  img{
    display:block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 400ms ease-out;
  }

  img:hover {
    transform: scale(1.15);
  }
}

@media screen and (min-width: 991px) {
  .wb-item{
    width:calc(100%/1 - 2rem);
  }
}

/*Wb-styling-ends*/

body, html{
  height:100%;
  margin:0;
  font-size:16px;
  font-family:helvetica, sans-serif;
  font-weight:400;
  line-height:1.6em;
  background: $lightColor2;
  color:$white;
}

/* navigation styles starts here */
  


/* navigation styles ends here */

/* home-page styles starts here */

.home-page {
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  h1 {
    color: $lightColor1;
    font-family: coolvetica, sans-serif;
    font-size: 50px;
    line-height: 70px;
  }

  h4 {
    color: $white;
    font-size: 26px;
    font-weight: 100;
  }

  p {
    color: $white;
    font-size: 1.25rem;
    line-height: 1.5;
  }

}

.about-section {
  padding: 100px 0;
  background: $darkColor1;

  p {
    color: $white;
    font-size: 1.25rem;
    line-height: 1.5;
  }

  .heading-title {
    text-align: left;
    margin-bottom: 0;

    h2 {
      color: $white;
      line-height: 64px;
    }

  }

}

.speciality-section{
  text-align: center;
  padding: 115px 0px 100px;

  h2 {
    color: $white;
  }
  
  .content{
    max-width: 610px;
    margin: 0 auto;
    padding:0 20px;

    p {
      color: $white;
      font-size: 1.25rem;
      line-height: 1.5;
    }
  }
}

.banner1{
  height:40vh;
  position:relative;
  background-position:center;
  background-size:cover;
  background-repeat:no-repeat;
  background-attachment:fixed;
}

/* home-page styles ends here */

/* footer styles starts here */

footer {
  background: $darkColor1;
  padding-top: 64px;

  .nav-link {
    display: inline;
    padding: 0;
  }

  .footer-top {
      padding: 30px 0 30px 0;
  }

  .heading-title {
    color: $white;
    padding-bottom: 30px;
  }

  .wb-contact {
  
    img {
      display: inline-block;
      height: 45px;
      font-size: 20px;
      text-align: center;
      line-height: 35px;
    }
  
    .mb-icon {
      border-radius: 5px;
    }
  
    .mm-icon {
      border-radius: 12px;
    }

  }

  .contact-details {
    color: $white;
    padding: 30px 0;
  }

  .address-details {
    color: $white;
    padding: 30px 0;
  }

  .social-media {
    padding-bottom: 40px;
  
    img {
      display: inline-block;
      width: 45px;
      height: 45px;
      cursor: pointer;
      font-size: 20px;
      text-align: center;
      line-height: 35px;
      margin-right: 20px;
      margin-bottom: 5px;
    }
  
    .fb-icon {
      border-radius: 5px;
      margin-bottom: 32px;
    }
  
    .in-icon {
      border-radius: 12px;
      margin-bottom: 32px;
    }

  }

  .footer-bottom{
    padding: 15px 20px;
    background: #232323;

    .copyright-text{
      color: $white;
    }
  }

}

/* footer styles ends here */

