.menu{
  padding: 10px 0;
  background: $lightColor2;
    
  .container{
    margin:0 auto;
    max-width:1200px;
    padding:0 1rem;
    
    .menu-item{
      margin:1rem;
      overflow: hidden;

      img{
        display:block;
        width: 100%;
        object-fit: cover;
      }

      .responsive-image{
        max-width:100%;
      }

      .menu-content-text{
        padding-top: .5rem;
  
        p {
          font-size: 1.25rem;
          line-height: 1.5;
        }
      }
    }

    @media screen and (min-width: 600px) {
      .menu-content{
        display:flex;
        flex-wrap:wrap;
        flex-direction:row;

        .menu-item{
          width:calc(50% - 2rem);
        }

        @media screen and (min-width: 1000px) {
          .menu-item{
            width:calc(100%/3 - 2rem);
          }
        }
      }
    }
  }
}

